@import '../base/variables.sass';

.large-hero {
    position: relative;

    &__text-block {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        width: 100%;
        text-align: center; }


    &__title {
        font-weight: 300;
        color: $sec-color;
        font-size: 4.8rem;
        margin: 0; }


    &__subtitle {
        font-weight: 300;
        color: $sec-color;
        font-size: 2.9rem;
        margin: 0; }


    &__paragraph {
        color: $accent-white;
        font-size: 1.875rem;
        font-weight: 100;
        text-shadow:  2px 2px 0 rgba(0,0,0,0.1);
        max-width: 30rem;
        margin-left: auto;
        margin-right: auto; } }



